import * as types from '../mutation-types'

const state = {
	device: {
		isMobile: false,
		isTablet: false
	},
	sidebar: {
		opened: false,
		hidden: false
	}
};

const mutations = {

	[types.APP_ACTION] (state, device) {
		console.log(JSON.stringify(state.device))
	}
};

export default {
	state,
	mutations
}



