const Test = resolve => require.ensure([], () => resolve(require('views/test.vue')), '/test');

const Index = resolve => require.ensure([], () => resolve(require('views/charts/index.vue')), '/index');

const Poo = resolve => require.ensure([], () => resolve(require('views/charts/Poo.vue')), '/poo');
const Pie = resolve => require.ensure([], () => resolve(require('views/charts/Pie.vue')), '/pie');

export default {
	name: '图表展示',
	path: '/chart',
	meta: {
		icon: 'fa-bar-chart-o',
		expanded: false,
		link: 'charts/index.vue'
	},
	component: Index,
		//function (resole) {
		//	require(['@/views/charts/index.vue'], resole)
		//},

	children: [
		{
			name: 'Poo',
			path: 'poo',
			component: Poo,
			//function (resole) {
			//	require(['@/views/charts/Pie.vue'], resole)
			//},
			meta: {
				link: 'chart/test.vue'
			}
		},
		{
			name: 'Pie',
			path: 'pie',
			component: Pie,
			//function (resole) {
			//	require(['@/views/charts/Pie.vue'], resole)
			//},
			meta: {
				link: 'chart/test.vue'
			}
		}
	]
}



