import * as types from '../../mutation-types'
import charts from './charts'


// import Test from '@/components/home/TEST.vue';
const Index = resolve => require.ensure([], () => resolve(require('views/sqoop/index.vue')), '/sqoop');

const Import = resolve => require.ensure([], () => resolve(require('views/sqoop/import.vue')), '/sqoop');
const Doc = resolve => require.ensure([], () => resolve(require('views/sqoop/Doc_part.vue')), '/sqoop');
const RDBMS = resolve => require.ensure([], () => resolve(require('views/sqoop/RDBMS_part.vue')), '/sqoop');



const state = {
	items: [
		// {
		// 	name: '占位链接',
		// 	path: '/index11111',
		// 	meta: {
		// 		icon: 'fa-tachometer',
		// 		link: 'dashboard/index.vue'
		// 	},
		// 	component: Test
		// }
		// ,

		{
			name: '自助数据源导入',
			path: '/sqoop',
			meta: {
				expanded: true,
			},
			component: Index
		},

		{
			name: '数据导入页面',
			path: '/import',
			meta: {
				expanded: false
			},
			component: Import
		},
		{
			name: '文本数据源',
			path: '/doc',
			meta: {
				expanded: false,
			},
			component: Doc
		},
		{
			name: '关系型数据库',
			path: '/rdbms',
			meta: {
				expanded: false,
			},
			component: RDBMS
		},
		charts
	]
};

const mutations = {
	[types.PRINT_MENU] (state,items) {
		console.log(JSON.stringify(state.items))
	}
};

export default {
	state,
	mutations
}



